import React, { Component } from 'react'
import Axios from 'axios';
import { Spin, Icon, Result, Alert } from 'antd';
import { Element, scroller, animateScroll as scroll } from "react-scroll"


export default class Forms extends Component {
    
    constructor(props){
        super(props);
        // this.resultRef = React.createRef();
        this.refrence = React.createRef();
        this.state = {
            response : {},
            message: null,
            gotResponse: false,
            registering: false,
            registered: false
        }

    }

    componentDidUpdate(){
        if(this.state.gotResponse){
            scroller.scrollTo('sub', {
                duration: 500,
                smooth: true,
                offset: 50, 
            })
        }
    }
    
    register = e => {
        this.setState({
            registering : true,
            registered : false,
            gotResponse : false 
        });
        e.preventDefault();
        let name = document.getElementById("name");
        let event = document.getElementById("event");
        let email = document.getElementById("email");
        let phone = document.getElementById("phone");
        let enrollment = document.getElementById("enrollment");
        var data = new FormData();
        data.append(name.id,name.value);
        data.append(event.id,event.value);
        data.append(email.id,email.value);
        data.append(phone.id,phone.value);
        data.append(enrollment.id,enrollment.value);
        console.log(data);
        Axios.post("https://script.google.com/macros/s/AKfycbzVulvLWx4cf80balepa1kMbr0Ktu_8Zr6st9wAoqSOa8tFzkZ8/exec",data)
        .then(res => {
            console.log(res.data)
            var responseData = res.data;
            if(responseData.ERROR){
                this.setState(
                    {
                        error: "SOMETHING WENT WRONG!"
                    }
                )
            }
            else{
                this.setState({
                    message: responseData,
                    registering: false,
                    registered: true
                })
            }
        })
        .catch(res =>{
            console.log(res)
        })
        this.setState({
            gotResponse: true
        })
        console.log(this.state)
    }

    render() {
        var classname = "";
        var submitbuttondata = "";
        var resullt = "";
        if(this.state.registering){
            const antIcon = <Icon type="loading" style={{ fontSize: 24 }} spin />;
            submitbuttondata = <Spin indicator={antIcon}/>
            classname = "btn btn-lg btn-white btn-icon mb-3 mb-sm-0";
        }
        else{
            submitbuttondata = 'SUBMIT'
            classname = "btn btn-lg btn-primary btn-icon mb-3 mb-sm-0";
            if(this.state.registered){
                if(this.state.message.isValid === true){
                    resullt = <Result
                                        icon={<Icon type="smile" theme="twoTone" />}
                                        title={"Registered Successfully! your ID = IT" + this.state.message.id}
                                    />
                }
                else{
                    let errorsR = this.state.message.isValid.toString().split('!');
                    errorsR.pop();
                    const listItems = errorsR.map((e) =>
                        <Alert key={e.toString()} message={e} type="error"/>
                    );
                    console.log(listItems);
                    resullt = <Result
                                    status="warning"
                                    title="Error."
                                    extra={
                                        <div>
                                            {listItems}
                                        </div>
                                    }
                                />
                }
            }
        }
        return (
   <>
    <hr/>
        <h2 style={{textAlign:'center'}}>REGISTER HERE</h2>
        <form id="regdata" style={{padding:'25px',margin:'25px'}} onSubmit={this.register}>
            <div className="row align-items-center">
              

                <div className="offset-md-2 col-md-8">
                      <h5>Event:</h5>
                      <select className="form-control mb-3" id="event" placeholder="select event" style={{color:'black'}}>
                            <option value="techquiz">Tech Quiz</option>
                            <option value="wysiwyg">WYSIWYG</option>
                            <option value="pyit">PYIT</option>
                      </select>
                </div>
                {/* <div className="offset-md-2 col-md-8">
                      <h5>Event:</h5>
                      <input type="text" className="form-control mb-3" id="event" style={{color:'black'}} placeholder="Event"/>
                </div> */}
                <div className="offset-md-2 col-md-8">
                      <h5>Name:</h5>
                      <input type="text" className="form-control mb-3" id="name" style={{color:'black'}} placeholder="Enter your name"/>
                </div>
                <div className="offset-md-2 col-md-8">
                      <h5>Enrollment:</h5>
                      <input type="number" className="form-control mb-3" id="enrollment" style={{color:'black'}} placeholder="Enter Your Enrollment Number"/>
                </div>
                <div className="offset-md-2 col-md-8">
                      <h5>Email:</h5>
                      <input type="email" className="form-control mb-3" id="email" style={{color:'black'}} placeholder="name@example.com"/>
                </div>
                <div className="offset-md-2 col-md-8">
                      <h5>Contact Number:</h5>
                      <input type="number" className="form-control mb-3" id="phone" style={{color:'black'}} placeholder="Your Mo. No."/>
                </div>
                <div className="offset-md-2 col-md-8" style={{textAlign:'center'}}>
                    {resullt}
                <Element name="sub" className="element" >
                    <button id='submit' className={classname} ref={this.refrence} style={{width: '200px',borderRadius: '25px'}}>
                        <span className="btn-inner--text">{submitbuttondata}</span>
                    </button>
                </Element>
                </div>
            </div>
        </form>



            {/* <section id="Input form" className="">
                <div id="main" className="row gtr-200">
                    <div className="col-6 col-12-medium">
                        <h1>Enter the fucking details for <p>Tech Quiz</p></h1>
                        <hr />
                        <form onSubmit={this.register}>
                            <input className="col-4 col-12-medium" type="email" placeholder="Enter your email" name="email" />
                            <br/><input className="col-4 col-12-medium" type="text" placeholder="Enter your name" name="name" />
                            <br/><input className="fit" type="text" placeholder="Enter your contact number" name="mo" />
                            <br/><input className="fit" type="text" placeholder="Enter your Enrollment number" name="enroll" />
                            <br/>
                            <input className="fit small primary" type="submit" />
                        </form>
                        {
                            this.state.gotResponse?
                            <div className="alert"><h1>
                            your id is :
                            {
                                this.state.message
                            }
                            </h1>
                            </div>:
                            null
                        }
                    </div>
                </div> 
            </section> */}
        </>
        )


    }
}
